import {Component, OnInit} from '@angular/core';
import {User} from '../../entity';
import {UserService} from '../../user.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-change-administrator',
  templateUrl: './change-administrator.component.html',
  styleUrls: ['./change-administrator.component.css']
})
export class ChangeAdministratorComponent implements OnInit {
  // tslint:disable-next-line:variable-name
  private administrator_user_id = window.localStorage.getItem('administrator_user_id');
  private administratorPassword: string;
  user: User;

  constructor(private userService: UserService,
              private location: Location) {
  }

  ngOnInit() {
    this.userService.getUserAdministratorById(this.administrator_user_id)
      .subscribe(res => this.user = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(oldPassword: string, newPassword: string, renewPassword: string): void {
    this.userService.getUserAdministratorById(this.administrator_user_id)
      .subscribe((res) => this.administratorPassword = res.password);
    if (!oldPassword || !newPassword || !renewPassword) {
      alert('!');
      return;
    } else if (oldPassword !== this.administratorPassword) {
      alert('!');
      return;
    } else if (newPassword !== renewPassword) {
      alert('!');
      return;
    } else {
      this.user.password = renewPassword;
      this.userService.changeAdministratorPassWord(this.user)
        .subscribe(res => {
          alert(res.message);
          this.goBack();
        });
    }
  }
}
