import {Component, DoCheck, OnInit} from '@angular/core';
import {Elective} from '../../entity';
import {ActivatedRoute, Router} from '@angular/router';
import {ElectiveService} from '../../elective.service';

@Component({
  selector: 'app-check',
  templateUrl: './check.component.html',
  styleUrls: ['./check.component.css']
})
export class CheckComponent implements OnInit, DoCheck {
  private electives: Elective[];
  private dataShow: Elective[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private electiveService: ElectiveService
  ) {
  }

  ngOnInit() {
    this.getElectives();
  }

  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.electives[startNum] !== null && this.electives[startNum] !== undefined) {
          this.dataShow.push(this.electives[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

  // Elective
  getElectives(): void {
    // tslint:disable-next-line:variable-name
    const student_id = window.localStorage.getItem('student_id');
    if (student_id === null) {
      alert('!');
      this.router.navigateByUrl('/');
    } else {
      this.electiveService.getElectives(student_id)
        .subscribe(res => {
          this.electives = res;
          this.leng = res.length;
          if (this.leng === 0) {
            alert('!');
          }
        });
    }
  }

  delete(elective: Elective | number): void {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('?');
    if (confirm_) {
      this.electiveService.deleteElective(elective)
        .subscribe(res => {
          alert(res.message); // 
          // 
          // if ( res.message === '') { //
          this.electives = this.electives.filter(h => h !== elective); // 
          this.dataShow = [];
          let startNum = (this.currentPag - 1) * this.pagNums;
          for (let i = 0; i < this.pagNums; i++, startNum++) {
            if (this.electives[startNum] !== null && this.electives[startNum] !== undefined) {
              this.dataShow.push(this.electives[startNum]);
            }
          }
          this.leng = this.electives.length;
        });
    } else {
      return;
    }
  }
}
