import {Component, DoCheck, OnInit} from '@angular/core';
import {Student} from '../entity';
import {StudentService} from '../student.service';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit, DoCheck {
  private students: Student[];
  private dataShow: Student[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(private studentService: StudentService) {
  }

  ngOnInit() {
    this.getStudents();
  }

// DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.students[startNum] !== null && this.students[startNum] !== undefined) {
          this.dataShow.push(this.students[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

// Student
  getStudents(): void {
    this.studentService.getStudents()
      .subscribe(res => {
        this.students = res;
        this.leng = res.length;
      });
  }

  delete(student: Student | number): void {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('?');
    if (confirm_) {
      this.studentService.deleteStudent(student)
        .subscribe(res => {
          alert(res.message); // 
          // 
          // if ( res.message === '') { //
          this.students = this.students.filter(h => h !== student); // 
          this.dataShow = [];
          let startNum = (this.currentPag - 1) * this.pagNums;
          for (let i = 0; i < this.pagNums; i++, startNum++) {
            if (this.students[startNum] !== null && this.students[startNum] !== undefined) {
              this.dataShow.push(this.students[startNum]);
            }
          }
          this.leng = this.students.length;
        });
    } else {
      return;
    }
  }
}
