import {Component, DoCheck, OnInit} from '@angular/core';
import {Course} from '../entity';
import {CourseService} from '../course.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.css']
})
export class CourseComponent implements OnInit, DoCheck {
  private courses: Course[];
  private dataShow: Course[]; // 
  public leng: number;                 // 
  public currentPag: number;           // 
  public pagNums: number;              // 
  public oldCurrentPag: number;
  public oldPagNums: number;

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService
  ) {
  }

  ngOnInit() {
    this.getCourses();
  }

  // DoCheck DoCheck
  ngDoCheck(): void {
    if (this.currentPag !== this.oldCurrentPag || this.pagNums !== this.oldPagNums) {
      this.dataShow = [];
      let startNum = (this.currentPag - 1) * this.pagNums;
      for (let i = 0; i < this.pagNums; i++, startNum++) {
        if (this.courses[startNum] !== null && this.courses[startNum] !== undefined) {
          this.dataShow.push(this.courses[startNum]);
        }
      }
      this.oldCurrentPag = this.currentPag;
      this.oldPagNums = this.pagNums;
    }
  }

// 
  currentPagHandel(pag: number) {
    this.currentPag = pag;
  }

// 
  pagNumsHandel(pagNum: number) {
    this.pagNums = pagNum;
  }

  // Course
  getCourses(): void {
    this.courseService.getCourses()
      .subscribe(res => {
        this.courses = res;
        this.leng = res.length;
        if (this.leng === 0) {
          alert('!');
        }
      });
  }

  delete(course: Course | number): void {
    // tslint:disable-next-line:variable-name
    const confirm_ = confirm('?');
    if (confirm_) {
      this.courseService.deleteCourse(course)
        .subscribe(res => {
          alert(res.message); // 
          // 
          // if ( res.message === '') { //
          this.courses = this.courses.filter(h => h !== course); // 
          this.dataShow = [];
          let startNum = (this.currentPag - 1) * this.pagNums;
          for (let i = 0; i < this.pagNums; i++, startNum++) {
            if (this.courses[startNum] !== null && this.courses[startNum] !== undefined) {
              this.dataShow.push(this.courses[startNum]);
            }
          }
          this.leng = this.courses.length;
        });
    } else {
      return;
    }
  }

}
